@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
::before,
::after {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	--red: hsl(0, 100%, 74%);
	--green: hsl(154, 59%, 51%);
	--blue: hsl(248, 32%, 49%);
	--dark-blue: hsl(249, 10%, 26%);
	--grayish-blue: hsl(246, 25%, 77%);

	--dark-blue-shadow: hsl(249, 10%, 26%, 0.3);
	--green-shadow: hsl(154, 59%, 45%, 1);

	--font-size: 16px;
	--font-family: "Poppins"; /*(https://fonts.google.com/specimen/Poppins)*/

	--element-width: 87%;
}

body {
	background-image: url(https://halivert.dev/react-intro-component-with-sign-up-form/static/media/bg-intro-mobile.02ce3982.png);
	background-color: hsl(0, 100%, 74%);
	background-color: var(--red);
	font-family: "Poppins";
	font-family: var(--font-family);
	font-size: 16px;
	font-size: var(--font-size);
}

.app {
	margin: 0;
	min-height: 100vh;
	display: grid;

	grid-template:
		"header" auto
		"main" 1fr
		"footer" auto
		/ 100%;

	grid-gap: 3.5em;

	gap: 3.5em;
}

.header {
	grid-area: header;
	color: white;
	display: flex;
	align-items: center;
	flex-direction: column;
	padding-top: 5.5em;
	grid-gap: 1.5em;
	gap: 1.5em;
}

.header__title {
	width: 80%;
	font-size: 1.7em;
	text-align: center;
	font-weight: 700;
	line-height: 1.3;
}

.header__content {
	width: 85%;
	text-align: center;
	line-height: 1.7;
	font-weight: 500;
}

.main {
	grid-area: main;
}

.footer {
	grid-area: footer;
	display: flex;
	color: white;
	padding: 0.5em;
	border-top: 1px solid hsl(0, 0%, 0%, 0.2);
	justify-content: center;
}

.footer__attribution {
	text-align: center;
	font-size: 0.8em;
}

.footer__link {
	color: hsl(249, 10%, 26%);
	color: var(--dark-blue);
	text-decoration: none;
}

.footer__link:focus,
.footer__link:focus-visible {
	outline: none;
	text-decoration: underline;
}

@media (min-width: 900px) {
	body {
		background-image: url(https://halivert.dev/react-intro-component-with-sign-up-form/static/media/bg-intro-desktop.059183ee.png);
	}

	.app {
		grid-template:
			"header main" 1fr
			"footer footer" auto
			/ 45% 55%;

		grid-gap: 3.5em;

		gap: 3.5em;
	}

	.header {
		justify-content: center;
		--margin-items: 10rem;
		--width-items: 80%;
	}

	.header__title {
		text-align: left;
		font-size: 3em;
		margin-left: var(--margin-items);
		width: var(--width-items);
	}

	.header__content {
		margin-left: var(--margin-items);
		width: var(--width-items);
		text-align: left;
		line-height: 1.7;
		font-weight: 500;
	}

	.main {
		display: flex;
		flex-flow: column;
		justify-content: center;
		align-items: flex-start;
		width: 80%;
		margin-top: 7em;
	}
}

.notification {
	display: flex;
	justify-content: center;
	margin-bottom: 1.75em;
	width: 100%;
}

.notification__body {
	background-color: var(--blue);
	color: white;
	width: var(--element-width);
	border-radius: 8px;
	padding: 1.5em 3em;
	text-align: center;
	box-shadow: 0 8px var(--dark-blue-shadow);
}

@media (min-width: 900px) {
	.notification {
		justify-content: flex-start;
	}
}

.input__field {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	width: 100%;
}

.input {
	--input-padding: 1.25em;

	font-family: var(--font-family);
	padding: var(--input-padding);
	font-weight: bold;
	border-radius: 4px;
	border: 1px solid var(--grayish-blue);
	outline: none;
	box-shadow: none;
	height: 100%;
	width: 100%;
}

.input:focus,
.input:focus-visible,
.input__field--has-errors .input {
	border-width: 2px;
	padding: calc(var(--input-padding) - 1px);
}

.input__control {
	position: relative;
	width: 100%;
}

.input__control:not(:last-child) {
	margin-bottom: 0.5em;
}

.input__field--has-errors .input__control::after {
	content: url(https://halivert.dev/react-intro-component-with-sign-up-form/static/media/icon-error.b72f792e.svg);
	position: absolute;
	right: 1em;
	top: 50%;
	transform: translateY(-40%);
}

.input__field--has-errors .input {
	border-color: var(--red);
	color: var(--red);
}

.input__errors-list {
	list-style: none;
}

.input__error {
	font-size: 0.7em;
	font-style: italic;
	color: var(--red);
	font-weight: 600;
}

.form {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
}

.form__fieldset {
	border: none;
	width: var(--element-width);
	display: flex;
	flex-direction: column;
	background-color: white;
	border-radius: 8px;
	box-shadow: 0 8px var(--dark-blue-shadow);
	padding: 1.25em 1.25em 1.5em 1.25em;
	grid-gap: 1em;
	gap: 1em;
}

.form__button {
	font-family: var(--font-family);
	color: white;
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	background-color: var(--green);
	border-style: none;
	border-radius: 4px;
	text-transform: uppercase;
	padding: 1.25em 0 1em 0;
	font-weight: 600;
	box-shadow: 0 4px var(--green-shadow);
	font-size: 0.95em;
	letter-spacing: 1px;
	cursor: pointer;
	outline: none;
}

.form__button:hover,
.form__button:focus,
.form__button:focus-visible {
	-webkit-filter: brightness(120%);
	        filter: brightness(120%);
}

.form__small {
	padding: 0 1em;
	font-size: 0.75em;
	text-align: center;
	color: var(--grayish-blue);
	font-weight: 500;
}

.form__link {
	text-decoration: none;
	color: var(--red);
	font-weight: bold;
	outline: none;
}

.form__link:focus,
.form__link:focus-visible {
	text-decoration: underline;
	-webkit-filter: brightness(110%);
	        filter: brightness(110%);
}

@media (min-width: 900px) {
	.form {
		align-items: flex-start;
	}

	.form__fieldset {
		padding: 2.25em;
		padding-bottom: 3em;
	}
}

